import React,{Component} from 'react';
import '../../css/detailTypes/detailType1.css';

class DetailType1 extends Component {
	constructor(){
		super();
		this.urls=[
		'http://localhost:8004/marketing_child1/getDetail',
		'http://localhost:8004/marketing_child2/getDetail',
		'http://localhost:8004/marketing_child3/getDetail',
		'http://localhost:8004/marketing_child4/getDetail'
		];
		this.prevUrls=[
		'http://localhost:8004/marketing_child1/getDetailPrev',
		'http://localhost:8004/marketing_child2/getDetailPrev',
		'http://localhost:8004/marketing_child3/getDetailPrev',
		'http://localhost:8004/marketing_child4/getDetailPrev'
		];
		this.nextUrls=[
		'http://localhost:8004/marketing_child1/getDetailNext',
		'http://localhost:8004/marketing_child2/getDetailNext',
		'http://localhost:8004/marketing_child3/getDetailNext',
		'http://localhost:8004/marketing_child4/getDetailNext'
		];
	}
	/*
	 *   
	 */
	handleBack(){
		this.refs.DT1_itemShow.style.opacity='0';
		setTimeout(()=>{
			this.refs.DT1_itemShow.style.display='none';
		},300)
	}
	
	/*
	 * 
	 */
	itemShow(num,type){
		/*
		 * num:
		 * type:Number   0id 1   -1  
		 */
		this.refs.DT1_itemShow.style.display='block';
		setTimeout(()=>{
			this.refs.DT1_itemShow.style.opacity='1';
		},300)
		this.num=num;
		this.getData(num,type)
	}
	/*
	 * 
	 */
	getData(num,type){
		switch(type){
			case 0:var Url=this.urls[this.props.num];break;
			case -1:var Url=this.prevUrls[this.props.num];break;
			case 1:var Url=this.nextUrls[this.props.num];break;
		}
		
		fetch(Url,{
			method:'post',
			headers:{
		    	"Content-type":"application/x-www-form-urlencoded; charset=UTF-8"
		    },
			body:`id=${num}`
		}).then(res=>{
			if(res.ok){
				return res.json()
			}
		}).then(res=>{
			if(!res.length) return;
			if(type== 1 || type == -1) this.num=res[0].id;
			this.refs.DT1_itemShow_con.innerHTML=res[0].content;
			this.setState({})
		}).catch(e=>{
			console.log(e)
		})
	}
	/*
	 * 
	 */
	prev(){
		this.getData(this.num,-1)
	}
	/*
	 * 
	 */
	next(){
		this.getData(this.num,1)
	}
	render(){
		this.bigNum=this.props.data.length;
		return (
			<div className='DetailType1'>
				<div className='DT1_items'>
					{/*
					  * 	  
					  */}
					{
						this.props.data.length&&
						this.props.data.map((v,k)=>(
							<div key={k} className='DT1_item' onClick={this.itemShow.bind(this,v.id,0)}>
								<div className='DT1_item_mesk'>
									{/*
									  * 	   
									  */}
									<div className='DT1_txt'>
										<h3>{v.tit}</h3>
										<p>{v.time}</p>
										<div className='line'></div>
										<p>{v.txt}</p>
									</div>
								</div>
								{/*
								  * 	
								  */}
								<img src={v.img}/>
							</div>
						))
					}
				</div>
				{/*
				  * 	
				  */}
				<div className='DT1_itemShow' ref='DT1_itemShow'>
					<div className='controller'>
						{/*
						  * 	
						  */}
						<a href='javascript:;' className='ctl_back' onClick={this.handleBack.bind(this)}>{(function(){
								var arr=[];
								for(var i=0;i<9;i++){
									arr.push(<span key={i}></span>)
								}
								return arr;
							}())}
						</a>
						{/*
						  * 	
						  */}
						<a href='javascript:;' className='ctl_prev' onClick={this.prev.bind(this)}>
							<span></span>
							<span></span>
						</a>
						{/*
						  * 	
						  */}
						<a href='javascript:;' className='ctl_next' onClick={this.next.bind(this)}>
							<span></span>
							<span></span>
						</a>
					</div>
					{/*
					  * 	
					  */}
					<div className='DT1_itemShow_con' ref='DT1_itemShow_con'>
					</div>
				</div>
			</div>
		)
	}
}
export default DetailType1;


